<template>
  <div>
    <input type="text" v-model="num1" /><br />
    <button @click="change">바꿈</button><br />
    <input type="text" v-model="num2" />
    <input type="text" maxlength="5" v-model="num4" /><span
  >{{ num4.length }}/5</span
  >
    <input
        type="checkbox"
        v-model="isAllChecked"
        value=""
        @click="allCheck"
    />전체체크
    <input
        type="checkbox"
        v-model="checkedValues"
        value="1"
        @change="clickFunc"
    />1
    <input
        type="checkbox"
        v-model="checkedValues"
        value="2"
        @change="clickFunc"
    />2
    <input
        type="checkbox"
        v-model="checkedValues"
        value="3"
        @change="clickFunc"
    />3
    <button @click="timeout()">인증</button>
    <p class="title">
      {{formatedMinute}}:{{formatedSecond}}
    </p>
    <button @click="startTimer">인증</button>

<!--    <button @click="stopTimer" :disabled="!timer">Stop</button>-->

<!--    <button @click="resetTimer" :disabled="timer">Reset</button>-->
  </div>
</template>

<script>
export default {
  data() {
    return {
      num1: '',
      num2: '',
      num3: 0,
      num4: '',
      set: ['', ''],
      isAllChecked: false,
      checkedValues: [],
      second: 0,
      minute: 1,
      timer: null,
    };
  },
  methods: {
    async change() {
      this.set[0] = this.num1;
      this.set[1] = this.num2;
      this.num1 = this.set[1];
      this.num2 = this.set[0];
    },
    clickFunc(event) {
      let checkedVal = {
        targetChecked: event.target.value,
        checkedArr: this.checkedValues,
      };
      this.$emit('checkClick', checkedVal);
    },
    allCheck() {
      if (!this.isAllChecked) {
        for (let i = 1; i < 4; i++) {
          this.checkedValues.push(i);
        }
        this.isAllChecked = true;
      } else {
        this.checkedValues = [];

        this.isAllChecked = false;
      }
      this.$emit('checkClick', this.checkedValues);
    },
    startTimer: function() {
      this.timer = setInterval(() => this.countdownSecond(), 1000);
    },
    countdownSecond: function() {
      this.second--;
      if(this.second < 0) {
        this.second = 59;
        this.countdownMinute();
      }
    },
    countdownMinute: function() {
      this.minute--;
    }
  },
  computed: {
    formatedSecond() {
      return String(this.second).padStart(2, '0');
    },
    formatedMinute() {
      return String(this.minute).padStart(2, '0');
    }
  },
};
</script>

<style></style>
